<template>
  <div
    class="modal my-popups centre fade"
    id="modalprojectquickviewpopaddreport"
    role="dialog"
    @click.self="closePopupOutside"
  >
    <div class="modal-dialog" role="document" :class="{'w-auto' : contentType === 'Date Filter'}">
      <div class="modal-content">
        <div class="modal-header">
          <button
            type="button"
            class="close"
            v-on:click="closePopup()"
            data-dismiss="modal"
          >
            <i class="fa fa-times" aria-hidden="true"></i>
          </button>
          <h4
            class="modal-title text-truncate"
            style="line-height: 26px"
            id="myModalLabel"
          >
            {{ contentType }}
          </h4>
        </div>
        <div class="modal-body">
          <div class="container-fluid p-0">
            <div v-if="contentType === 'Date Filter'">
              <daterangereport
                pgaction="global" :popupItem="popupItem"
                @set-date-report="SetDateRangeReport"
              ></daterangereport>
            </div>
            <div v-else-if="contentType === 'Data Filter'">
              <div>Content 2</div>
            </div>
            <div v-else>
              <div class="user_type_choice mt-2 coloredicons">
                <div
                  data-toggle="buttons"
                  class="btn-group btn-group-toggle custom-reports-icons my-2"
                  v-for="option in GraphViewList"
                  :key="option.graph_id"
                >
                <!-- v-if="shouldDisplayGraphView(option)" -->
                  <label
                    for="cls_usertype"
                    :class="[
                      {
                        active: capitalizeChartType(popupItem.chartType) === option.graph_name ? option.graph_name : popupItem.chartType === null && option.graph_name === 'Table',
                      },
                    ]"
                    class="btn btn_cstm GraphViewList"
                    @click="
                      selectGraphViewLists(option.graph_name);
                    "
                    :id="option.graph_id"
                  >
                    <input
                      type="radio"
                      :name="option.graph_id"
                      :id="option.graph_id"
                      :value="option.graph_id"
                    />
                    <span class="d-block pb-2">
                      <em :class="option.graph_image + ' ' +'bg-transparent'"></em>
                    </span>
                    {{ $t(option.graph_name) }}
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <loader v-if="isLoading"></loader>
  </div>
</template>
<style scoped>
.custom-reports-icons {
    min-width: inherit;
    height: 104px;
    width: 33%;
}
</style>
<script>
import daterangereport from "./DateRangeReport.vue";
export default {
  components: {
    daterangereport,
  },
  props: {
    popupItem: null,
    contentType: String,
    moduleCode:null,
    index:null,
    dashboardid: null,
  },
  data: function () {
    return {
      hoveredGraphView: null,
      isLoading: false,
      fromDate:null,
      toDate:null,
      GraphViewList: [
        {
          graph_id: 1,
          graph_type: "line",
          graph_name: "Line",
          graph_image: "fa fa-line-chart"
        },
        {
          graph_id: 2,
          graph_type: "bar",
          graph_name: "Bar",
          graph_image: "fa fa-bar-chart"
        },
        {
          graph_id: 3,
          graph_type: "horizontal_bar",
          graph_name: "Horizontal_Bar",
          graph_image: "fa fa-bar-chart"
        },
        {
          graph_id: 4,
          graph_type: "pie",
          graph_name: "Pie",
          graph_image: "fa fa-pie-chart"
        },
        {
          graph_id: 5,
          graph_type: "donut",
          graph_name: "Donut",
          graph_image: "fa fa-circle-o-notch"
        },
        {
          graph_id: 6,
          graph_type: "table",
          graph_name: "Table",
          graph_image: "fa fa-bars"
        },
      ],
    };
  },
  created() {
    // this.GetDashboard();
  },
  mounted() {
    // eslint-disable-next-line no-undef
    $("#modalprojectquickviewpopaddreport").modal("show");
  },
  methods: {
    closePopupOutside(event) {
      // Check if the click target is outside of the modal
      if (!event.target.closest('.modal-dialog')) {
        // If yes, close the modal by setting showpopup to false
        this.$emit("close-popup");
      }
    },
  capitalizeChartType(chartType) {
      // Check if chartType is not null or undefined
      if (chartType) {
        // Capitalize the first letter
        return chartType.charAt(0).toUpperCase() + chartType.slice(1);
      } else {
        return "";
      }
    },
    SetDateRangeReport(from, to, dateSpan) {
      this.popupItem.fromDate = from;
      this.popupItem.toDate = to;
      this.popupItem.dateSpan = dateSpan.UserInputAction;
    },
    setHoveredGraphView(graphName) {
      this.hoveredGraphView = graphName;
    },
    clearHoveredGraphView() {
      this.hoveredGraphView = null;
    },
    closePopup() {
      this.$emit("close-popup");
    },
    selectGraphViewLists(chart_type) {
      this.popupItem.chartType = chart_type;
      this.saveChangeChart();
    },
    saveChangeChart() {
      this.$emit("close-popup");
    },
  },
};
</script>